import React from 'react';
import './employees-list-item.css';

class EmployeesListItem extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      salary: this.props.salary.includes('$') ?
              this.props.salary :
              this.props.salary + '$'
    }
  }

  onNewSalary = (e) => {
    this.setState({
      salary: e.target.value
    })
    this.props.onNewSalary(e.target.value);
  }

  render() {
  const {name, onDelete, onToggleIncrease, onTogglePromotion, increase, promotion} = this.props;
  const { salary } = this.state;

  let classNames = 'list-group-item d-flex justify-content-between';
  if (increase) {
    classNames += ' increase'
  }
  if (promotion) {
    classNames += ' like'
  }

  return (
    <li className={classNames}>
      <span 
        onClick={onTogglePromotion} 
        className="list-group-item-label"
        title="Click to mark for promotion">
          {name}
      </span>
      <input 
            type="text" 
            className="list-group-item-input" 
            value={salary}
            onChange={this.onNewSalary}
            title="You can change the value"/>
      <div className='d-flex justify-content-center align-items-center'>
        <button type="button"
            className="btn-cookie btn-sm"
            onClick={onToggleIncrease}
            title="Click on for promotion">
            <i className="fas fa-cookie"></i>
        </button>

        <button type="button"
            className="btn-trash btn-sm "
            onClick={onDelete}
            title="Click to remove">
            <i className="fas fa-trash"></i>
        </button>
        <i className="fas fa-star"></i>
      </div>
    </li>
  )
  }
}

export default EmployeesListItem;